.searchbox-dropdown {
  box-shadow: #a5adba 0px 1px 5px;
  background: #ffffff;
  padding-top: 10px;
  overflow: hidden;
  border-radius: 4px;
}
.searchbox-dropdown .searchbox-dropdown-container {
  position: relative;
}
.searchbox-dropdown .searchbox-dropdown-container input {
  position: absolute;
  z-index: 2222;
  width: 90%;
  margin-left: 10px;
}
.searchbox-dropdown .searchbox-dropdown-container .searchbox-dropdown-tree {
  height: 250px;
  overflow: auto;
  padding-top: 25px;
}
.searchbox-dropdown .not-leaf .u-tree-checkbox {
  visibility: hidden;
}
