.new-tagbugton-control .u-tag {
  white-space: nowrap;
  max-width: none;
}
.new-tagbugton-control .new-tagbutton-control-icon {
  display: inline-block;
  vertical-align: middle;
  line-height: 20px;
}
.new-tagbugton-control .new-tagbutton-control-icon .s-popover-inner-span {
  display: block;
  line-height: 20px;
}
.new-tagbugton-control .new-tagbutton-control-icon .s-popover-inner-span i.yonicon {
  display: inline-block;
  line-height: 20px;
  width: 20px;
  height: 20px;
  padding: 0;
  border-radius: 4px;
  font-size: 12px;
  color: #505766;
  cursor: pointer;
}
.new-tagbugton-control .new-tagbutton-control-icon .s-popover-inner-span i.yonicon:hover {
  background: #f8f8f8;
}
.new-tagbutton-popover .tooltip-arrow {
  display: none;
}
.new-tagbutton-popover .u-popover-content .u-tag-checkable {
  border: none;
  border-radius: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  padding: 0 16px;
  margin-bottom: 0 !important;
  text-align: left;
}
.new-tagbutton-popover .u-popover-content .u-tag-checkable:hover {
  background: #f8f9fd;
}
.new-tag-group.tag-group > .yon-row {
  min-width: 270px;
}
.new-tagbutton-popover .tooltip-inner {
  min-width: auto!important;
}
.new-tagbutton-popover .tooltip-inner .u-tag {
  display: block;
}
