.split_container {
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
}
.split_container .split_left {
  width: 240px;
  height: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.split_container .split_left.hideLeft > .left-container {
  display: none;
}
.split_container .split_left .left-container {
  display: block;
}
.split_container .split_left .left-container .border-r {
  height: 100%;
}
.split_container .split_left .left-container > .meta-default-container {
  height: 100%;
}
.split_container .split_left .viewCell.width-percent-100 .control-width .ac-input-locale,
.split_container .split_left .viewCell.width-percent-100 .control-width .ant-select,
.split_container .split_left .viewCell.width-percent-100 .control-width .u-form-control,
.split_container .split_left .viewCell.width-percent-100 .control-width .u-input-group,
.split_container .split_left .viewCell.width-percent-100 .control-width .u-select {
  width: auto !important;
}
.split_container .split_right {
  overflow: auto;
  height: 100%;
}
.split_container .split_right .list-top-toolbar.simple-query {
  width: 100% !important;
}
.split_container .transverse {
  width: 10px;
}
.split_container .transverse .border_line {
  border-right: 1px solid #d9d9d9;
}
.split_container.hide_split_right .split_left {
  width: 100%!important;
}
.split_container.hide_split_right .split_left .left-container > .meta-default-container {
  width: 100%!important;
}
.split_container.hide_split_right .transverse {
  display: none;
}
.split_container.hide_split_right .split_right {
  display: none;
}
