.refer-taglist {
  width: 100%;
  height: 100%;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  background: #fff;
  position: relative;
  overflow: hidden;
}
.refer-taglist .tagBtn-add-disabled.tagBtn-add {
  visibility: hidden;
}
.refer-taglist .tagBtn-add {
  border: none !important;
  height: 26px !important;
  border-radius: 18px;
  min-width: 25px !important;
  float: right;
  padding: 0 !important;
  position: absolute;
  right: 0;
  top: 0;
}
.refer-taglist .tagBtn-add.u-button-border {
  background: transparent;
  right: 0;
}
.refer-taglist .tagBtn-add.u-button-border i {
  width: 20px;
}
.refer-taglist .tagBtn-add i {
  margin-left: 0px;
  display: block;
}
.refer-taglist .yonicon-close-circle {
  right: 25px!important;
  position: absolute;
  top: 7px;
}
.refer-taglist .tag-container {
  position: relative;
  line-height: 26px;
  width: calc(100% - 42px);
  display: -ms-flexbox;
  display: flex;
}
.refer-taglist .tag-container .ant-select-selection-selected-value {
  padding-right: 0px;
}
.refer-taglist .tag-container .yonicon-ellipsis {
  margin-right: 0px;
}
.refer-taglist .tag-container .u-tag {
  display: -ms-inline-flexbox;
  display: inline-flex;
  vertical-align: top;
  margin: 3px 0 2px 6px;
  background-color: #F7F9FD;
  border: 1px solid #DBE0E5;
  padding: 0px 3px 0px 5px;
  border-radius: 3px;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
}
.refer-taglist .tag-container .u-tag.lastTag .u-tag-text,
.refer-taglist .tag-container .u-tag.only-one-tag .u-tag-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.refer-taglist .tag-container .u-tag:first-child {
  margin-left: 4px;
}
.refer-taglist .tag-container .refer-multiple-input {
  -ms-flex: 1;
      flex: 1;
  position: relative;
}
.refer-taglist .tag-container .refer-multiple-input .u-select-search-field input {
  height: 26px;
  padding: 0px;
  padding-left: 5px;
  min-width: 3px;
  border-width: 0px;
}
.refer-taglist.disabled {
  background-color: #f7f7f7;
  color: #999 !important;
}
.new-filter-container .refer-taglist.disabled:hover {
  border: 1px solid #d9d9d9 !important;
}
.tags_menu .u-tag {
  max-width: 100%;
}
div.tags_dropMenu .tags_menu.multi-refer-tag-menu .tag_item .u-tag span.u-tag-text {
  max-width: 90%;
}
.has-error .refer-taglist {
  border-color: #f44336;
}
.referSingle .canzhaoSingle {
  margin-left: 4px;
}
.u-modal .referModal .u-modal-content {
  display: block;
}
#yxyweb-support-container .referModal.refer-modal-maximize {
  position: absolute!important;
  top: 0;
  bottom: 8px;
  left: 8px;
  right: 8px;
  z-index: 1000;
  width: auto!important;
  margin: 0!important;
}
.refer-taglist.disabled .yonicon-close-circle {
  display: none;
}
